      }).catch((err) => {
        console.log(err)
      })
    },
    piliangdaochuDelete(){
      webMy.readAll().then((result) => {
        console.log(result.length)
        for(let a in result){
          webMy.remove(result[a].key).then((result) => {
            console.log('delete')
          }).catch((err) => {
            console.log(err)
          })
        }
      }).catch((err) => {
        console.log(err)
      })
    },
