import { product_list } from './product_list.js'
import axios from 'axios'

export default {
  name: "AlbionOnline",
  data() {
    return {
      Lymhurst: Lymhurst,
      Bridgewatch: Bridgewatch,
      Martlock: Martlock,
      Thetford: Thetford,
      // Lymhurst: {P1:[],P2:[],P3:[],P4:[],P5:[],P6:[],P7:[],P8:[]},
      // BlackMarket: {P1:[],P2:[],P3:[],P4:[],P5:[],P6:[],P7:[],P8:[]},
      // Bridgewatch: {P1:[],P2:[],P3:[],P4:[],P5:[],P6:[],P7:[],P8:[]},
      // Caerleon: {P1:[],P2:[],P3:[],P4:[],P5:[],P6:[],P7:[],P8:[]},
      // FortSterling: {P1:[],P2:[],P3:[],P4:[],P5:[],P6:[],P7:[],P8:[]},
      // Martlock: {P1:[],P2:[],P3:[],P4:[],P5:[],P6:[],P7:[],P8:[]},
      // Thetford: {P1:[],P2:[],P3:[],P4:[],P5:[],P6:[],P7:[],P8:[]},
      activeKey: [],
      Lymhurst_list: [],
      Bridgewatch_list: [],
      Martlock_list:[],
      FortSterling_list:[],
      Thetford_list:[],

      city_spread: [],
      Lymhurst_spread: [],
      Bridgewatch_spread: [],
      Martlock_spread: [],
      Thetford_spread: [],
    };
  },
  mixins: [
    
  ],
  methods: {
    async retrieve_data(){
      for(let a in product_list){
        for(let b in product_list[a]){
          let request = await axios.get('https://www.albion-online-data.com/api/v1/stats/prices/'+product_list[a][b].UniqueName);
          let res = request.data;
          // console.log(res);
          for(let c in res){
            switch(res[c].city){
              case 'Black Market':
                if(res[c].sell_price_min != 0 && res[c].buy_price_max != 0){
                  this.BlackMarket[a].push({
                    SELL: res[c].sell_price_min,
                    BUY: res[c].buy_price_max,
                    name: product_list[a][b].LocalizedNames['ZH-CN'],
                    UniqueName: product_list[a][b].UniqueName
                  })
                }
                break;
              case 'Bridgewatch':
                if(res[c].sell_price_min != 0 && res[c].buy_price_max != 0){
                  this.Bridgewatch[a].push({
                    SELL: res[c].sell_price_min,
                    BUY: res[c].buy_price_max,
                    name: product_list[a][b].LocalizedNames['ZH-CN'],
                    UniqueName: product_list[a][b].UniqueName
                  })
                }
                break;
              case 'Caerleon':
                if(res[c].sell_price_min != 0 && res[c].buy_price_max != 0){
                  this.Caerleon[a].push({
                    SELL: res[c].sell_price_min,
                    BUY: res[c].buy_price_max,
                    name: product_list[a][b].LocalizedNames['ZH-CN'],
                    UniqueName: product_list[a][b].UniqueName
                  })
                }
                break;
              case 'Fort Sterling':
                if(res[c].sell_price_min != 0 && res[c].buy_price_max != 0){
                  this.FortSterling[a].push({
                    SELL: res[c].sell_price_min,
                    BUY: res[c].buy_price_max,
                    name: product_list[a][b].LocalizedNames['ZH-CN'],
                    UniqueName: product_list[a][b].UniqueName
                  })
                }
                break;
              case 'Lymhurst':
                if(res[c].buy_price_max != '0' && res[c].sell_price_min != '0'){
                  this.Lymhurst[a].push({
                    SELL: res[c].sell_price_min,
                    BUY: res[c].buy_price_max,
                    name: product_list[a][b].LocalizedNames['ZH-CN'],
                    UniqueName: product_list[a][b].UniqueName
                  })
                }
                break;
              case 'Martlock':
                if(res[c].sell_price_min != 0 && res[c].buy_price_max != 0){
                  this.Martlock[a].push({
                    SELL: res[c].sell_price_min,
                    BUY: res[c].buy_price_max,
                    name: product_list[a][b].LocalizedNames['ZH-CN'],
                    UniqueName: product_list[a][b].UniqueName
                  })
                }
                break;
              case 'Thetford':
                if(res[c].sell_price_min != 0 && res[c].buy_price_max != 0){
                  this.Thetford[a].push({
                    SELL: res[c].sell_price_min,
                    BUY: res[c].buy_price_max,
                    name: product_list[a][b].LocalizedNames['ZH-CN'],
                    UniqueName: product_list[a][b].UniqueName
                  })
                }
                break;
            }
          }
        }
      }
      this.Lymhurst_spread = this.买卖价差商品(this.Lymhurst);
      this.Bridgewatch_spread = this.买卖价差商品(this.Bridgewatch);
      this.Martlock_spread = this.买卖价差商品(this.Martlock);
      this.city_spread = this.城市间商品价差({
        Lymhurst: this.Lymhurst,
        Bridgewatch: this.Bridgewatch,
        Martlock: this.Martlock,
        FortSterling: this.Martlock,
        Thetford: this.Martlock,
        Caerleon: this.Martlock,
        BlackMarket: this.Martlock,
      });
    },
    bubbleSort(arr) {
      var len = arr.length;
      for (var i = 0; i < len - 1; i++) {
        for (var j = 0; j < len - 1 - i; j++) {
          if (arr[j].spread_percentage < arr[j+1].spread_percentage) {
            var temp = arr[j+1];
            arr[j+1] = arr[j];
            arr[j] = temp;
          }
        }
      }
      return arr;
    },
    成交总价(number){
      if(number>1000000 && number<=5000000){
        return '#009688'
      }else if(number>5000000 && number<=10000000){
        return '#4caf50'
      }else if(number>10000000 && number<=20000000){
        return '#8bc34a'
      }else if(number>20000000 && number<=50000000){
        return '#cddc39'
      }else if(number>50000000 && number<=100000000){
        return '#ffeb3b'
      }else if(number>100000000 && number<=200000000){
        return '#ffc107'
      }else if(number>200000000 && number<=400000000){
        return '#ff9800'
      }else if(number>400000000 && number<=800000000){
        return '#ff5722'
      }else if(number>800000000 && number<=1200000000){
        return '#ff5722'
      }else if(number>1200000000 && number<=2000000000){
        return '#ff1100'
      }
    },
    买卖价差商品(data){
      let array = [];
      for(let a in data){
        let grade = a,list = data[a];
        for(let b in list){
          let json = {
            grade: grade,
            name: list[b].name,
            SELL: list[b].SELL,
            BUY: list[b].BUY,
            spread: list[b].SELL - list[b].BUY,
            spread_percentage: parseInt((1-(list[b].BUY/list[b].SELL))*100)
          };
          array.push(json)
        }
      }
      return this.bubbleSort(array)
    },
    城市间商品价差(data){
      let array = [];
      for(let a in data){
        let city_name = a,city_data = data[a];
        for(let b in city_data){
          let grade_name = b,grade_data = city_data[b];
          for(let c in grade_data){
            let list = grade_data[c];
            array.push({
              city_name: city_name,
              grade_name: grade_name,
              name:list.name,
              SELL:list.SELL,
              BUY:list.BUY
            })
          }
        }
      }
      let 数据重组 = [];
      for(let z in array){
        let name = array[z].name,grade_name = array[z].grade_name,data = [];
        for(let y in array){
          if(array[y].name == name && array[y].grade_name == grade_name){
            data.push(array[y]);
            array.splice(y, 1);
          }
        }
        let data_length = data.length;
        for(let x = 0;x<data_length;x++){
          const after = data.shift();
          if(data.length>0){
            for(let w in data){
              data[w]
              let come = data[w].SELL-after.BUY,
              go = after.SELL-data[w].BUY,
              city_come_go = '',spread='',spread_percentage='';

              if(come>go){
                city_come_go = after.city_name+'=>'+data[w].city_name;
                spread = come;
                spread_percentage = after.BUY/data[w].SELL;
              }else{
                city_come_go = data[w].city_name+'=>'+after.city_name;
                spread = go;
                spread_percentage = data[w].BUY/after.SELL;
              }
              let json = {
                city: city_come_go,
                grade: after.grade_name,
                name: after.name,
                SELL: `${after.city_name}:${after.SELL};${data[w].city_name}:${data[w].SELL};`,
                BUY: `${after.city_name}:${after.BUY};${data[w].city_name}:${data[w].BUY};`,
                spread: spread,
                spread_percentage: parseInt((1-spread_percentage)*100)
              };
              数据重组.push(json)
            }
          }
        }
      }
      return this.bubbleSort(数据重组)
    }
  },
  watch: {
    
  },
  created(){
    // this.retrieve_data();
    this.Lymhurst_spread = this.买卖价差商品(this.Lymhurst);
    this.Bridgewatch_spread = this.买卖价差商品(this.Bridgewatch);
    this.Martlock_spread = this.买卖价差商品(this.Martlock);
    this.Thetford_spread = this.买卖价差商品(this.Thetford);
    this.city_spread = this.城市间商品价差({
      Lymhurst: this.Lymhurst,
      Bridgewatch: this.Bridgewatch,
      // Martlock: this.Martlock,
      // FortSterling: this.Martlock,
      Thetford: this.Thetford,
      // Caerleon: this.Martlock,
      // BlackMarket: this.Martlock,
    });
    // let all = 0;
    // for(let a in Lymhurst){
    //   for(let b in Lymhurst[a]){
    //     all += (Lymhurst[a][b].Volume*Lymhurst[a][b].averagePrice);
    //   }
    // }
    // console.log(all)
  },
  mounted() {
    
  }
};
</script>

