<template>
  <div class="hello">
    <h3>demo 2</h3>
    <h3>{{ test1 }}</h3>
    <h3>{{ test2 }}</h3>
    <div style="    display: inline-block;
    padding: 6px 20px;margin-top: 10px;cursor: pointer;
    border: 1px solid #000;"
    @click="test1click">test1 ++</div>
    <div style="    display: inline-block;
    padding: 6px 20px;margin-top: 10px;margin-left: 10px;cursor: pointer;
    border: 1px solid #000;"
    @click="test2click">test2.a ++</div>
  </div>
</template>

<script>
import Amx from 'vue-amx'
export default {
  name: "vueamxdemo1",
  data() {
    return {
    };
  },
  mixins:[Amx.read('test1'),Amx.read('test2')],
  methods: {
    test1click () {
      this.test1 ++
    },
    test2click () {
      this.test2.a ++
    },
  },
  mounted(){
    console.log(Amx.read('test1').data())
  },
  created(){
  }
};
</script>
<style scoped>
.hello {
  padding: 20px;
}
</style>


