<template>
  <div class="videoa">
    <h1>會動的數字</h1>
    <div class="devdom" :style="{'--percent': devnumber}"></div>
    <a-input v-model="devnumber" placeholder="會動的數字" />
  </div>
</template>

<script>
export default {
  name: "videoa",
  data() {
    return {
      devnumber: 0,
    };
  },
  mixins: [
  ],
  methods: {
  },
  watch: {
  },
  mounted() {
  }
};
</script>

<style scoped>
@property --percent {
  syntax: "<number>";
  initial-value: 0;
  inherits: false;
}
@property --temp {
  syntax: "<number>";
  initial-value: 0;
  inherits: false;
}
@property --v1 {
  syntax: "<integer>";
  initial-value: 0;
  inherits: false;
}
@property --v2 {
  syntax: "<integer>";
  initial-value: 0;
  inherits: false;
}
.devdom {
  font: 800 40px monospace;
  padding: 2rem;
  transition: --percent 1s;
  --temp: calc(var(--percent) * 100);
  --v1: max(var(--temp) - 0.5, 0);
  --v2: max((var(--temp) - var(--v1)) * 100 - 0.5, 0);
  counter-reset: v1 var(--v1) v2 var(--v2);
}
.devdom::before {
  content: counter(v1) "." counter(v2, decimal-leading-zero) "%";
}
</style>



